import HomePage from '../pages/home.f7.html';
import BlockedPage from '../pages/blocked.f7.html';
import Framework7 from 'framework7/framework7.esm.bundle.js';

const commonErrorMessage = "The app you were looking for is not available anymore. It might has been deleted. Please refer to your app developer for any question.";
const unknownErrorMessage = "Something went wrong. Please try again.";
const invalidURLErrorMessage = "Invalid URL. Please double-check your app URL.";
const dropboxAccessErrorMessage = "Uh oh! We couldn't load application information! Please make sure you are connected to the Internet or dropbox.com & dl.dropboxusercontent.com is not blocked and try again.";

let adDiv = `
<div class="text-align-center" style="margin-top: 25px;">
    <!--<p> advertisement </p>-->
    <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
    <ins class="adsbygoogle adslot_1" style="display:inline-block" data-ad-client="ca-pub-3139212365885959" data-ad-slot="5702922209" data-full-width-responsive="true"></ins>
    <script async type="text/javascript">(adsbygoogle = window.adsbygoogle || []).push({});</script>
</div>
`;

let dbBaseURL = "https://appbox-proxy-server.herokuapp.com/https://dl.dropboxusercontent.com"

var appRequest = function (routeTo, routeFrom, resolve, reject) {
    // Router instance
    var router = this;

    // App instance
    var app = router.app;

    // Check Activity Response
    var response = { result: 1, score: 0.0 };
    grecaptcha.ready(function () {
        try {
            grecaptcha.execute('6LeEELYaAAAAAPYX6lUAOILelkPL40UyXdCkPj0m').then(function (token) {
                var client = new XMLHttpRequest();
                client.timeout = 60000;
                client.onreadystatechange = function () {
                    if (client.readyState == 4) {
                        if (client.status == 200 || client.status == 400) {
                            try {
                                response = JSON.parse(client.responseText);
                                if (response && ((response.result == 1 && response.score < 0.3) || response.result != 1)) {
                                    adDiv = '';
                                    dbrequest(router, app, routeTo, routeFrom, resolve, reject);
                                } else {
                                    dbrequest(router, app, routeTo, routeFrom, resolve, reject);
                                }
                            } catch (err) {
                                dbrequest(router, app, routeTo, routeFrom, resolve, reject);
                            }
                        } else if (client.status == 0) {
                            adDiv = '';
                            dbrequest(router, app, routeTo, routeFrom, resolve, reject);
                            return;
                        } else {
                            dbrequest(router, app, routeTo, routeFrom, resolve, reject);
                        }
                    }
                };
                client.open('POST', 'https://us-central1-appbox-recaptcha.cloudfunctions.net/verify?response=' + token);
                client.send();
            });
        } catch (error) {
            console.log(error);
            dbrequest(router, app, routeTo, routeFrom, resolve, reject);
        }
    });
}

function dbrequest(router, app, routeTo, routeFrom, resolve, reject) {
    //Check if app details already loaded
    if (router.appDetail) {
        processUsing(app, router.appDetail, resolve, reject, false);
        return;
    }

    // Show Preloader
    app.preloader.show();

    // get app query
    const appQueryURL = routeTo.query.url;

    // app detail object
    let appDetail = [];
    appDetail.adDiv = adDiv;
    if (appQueryURL) {
        if (appQueryURL.indexOf('manifest.plist') != -1) {
            var client = new XMLHttpRequest();
            client.timeout = 60000;
            client.onreadystatechange = function () {
                if (client.readyState == 4 && client.status == 200) {
                    try {
                        let response = client.responseText.replace(new RegExp(/[^a-zA-Z0-9._]/g), '');
                        appDetail.response = response;
                        appDetail.menifest = appQueryURL;
                        appDetail.name = response.split('stringkeytitlekeystring')[1].toString().split('string')[0];
                        appDetail.identifier = response.split('keybundleidentifierkeystring')[1].toString().split('string')[0];
                        appDetail.version = response.split('stringkeybundleversionkeystring')[1].toString().split('string')[0];
                        router.appDetail = appDetail;
                        processUsing(app, appDetail, resolve, reject, true);
                    }
                    catch (err) {
                        appDetail.errorMessage = unknownErrorMessage;
                        appDetail.error = err;
                        router.appDetail = appDetail;
                        processUsing(app, appDetail, resolve, reject, true);
                    }
                } else if (client.readyState == 4 && client.status == 0) {
                    appDetail.errorMessage = dropboxAccessErrorMessage;
                    router.appDetail = appDetail;
                    processUsing(app, appDetail, resolve, reject, true);
                } else if (client.status >= 400) {
                    appDetail.errorMessage = commonErrorMessage;
                    router.appDetail = appDetail;
                    processUsing(app, appDetail, resolve, reject, true);
                }
            };
            client.open('GET', dbBaseURL + appQueryURL);
            client.setRequestHeader('Content-Type', 'text/xml');
            client.overrideMimeType('text/xml')
            client.send();
        } else if (appQueryURL.indexOf('appinfo.json') != -1) {
            var client = new XMLHttpRequest();
            client.timeout = 60000;
            client.onreadystatechange = function () {
                if (client.readyState == 4 && client.status == 200) {
                    try {
                        //response
                        var response = JSON.parse(client.responseText);
                        appDetail.response = response;
                        simplifedAppResponse(appDetail, response.latestVersion, appDetail);

                        //set other version details
                        if (response.versions.length > 1) {
                            appDetail.allVersions = [];
                            response.versions.reverse();
                            response.versions.forEach(element => {
                                var simplifedVersion = [];
                                simplifedAppResponse(appDetail, element, simplifedVersion);
                                appDetail.allVersions.push(simplifedVersion);
                            });
                        }

                        router.appDetail = appDetail;
                        processUsing(app, appDetail, resolve, reject, true);
                    }
                    catch (err) {
                        appDetail.errorMessage = unknownErrorMessage;
                        appDetail.error = err;
                        router.appDetail = appDetail;
                        processUsing(app, appDetail, resolve, reject, true);
                    }
                } else if (client.readyState == 4 && client.status == 0) {
                    appDetail.errorMessage = dropboxAccessErrorMessage;
                    router.appDetail = appDetail;
                    processUsing(app, appDetail, resolve, reject, true);
                }
                else if (client.status >= 400) {
                    appDetail.errorMessage = commonErrorMessage;
                    router.appDetail = appDetail;
                    processUsing(app, appDetail, resolve, reject, true);
                }
            };
            client.open('GET', dbBaseURL + appQueryURL);
            client.setRequestHeader('Content-Type', 'text/xml');
            client.overrideMimeType('text/xml')
            client.send();
        } else {
            appDetail.errorMessage = invalidURLErrorMessage;
            router.appDetail = appDetail;
            processUsing(app, appDetail, resolve, reject, true);
        }
    } else {
        appDetail.errorMessage = invalidURLErrorMessage;
        router.appDetail = appDetail;
        processUsing(app, appDetail, resolve, reject, true);
    }
}

function processUsing(app, appDetail, resolve, reject, firstLoad) {
    app.detail = appDetail;
    // Create toast with button
    var message = '';

    //Update page name
    if (appDetail.name && appDetail.name.length > 0) {
        document.title = appDetail.name + ' | AppBox';
    }

    //gtag page name
    gtag('config', 'UA-85215717-1');

    if (appDetail.supportedDevice && appDetail.supportedDevice.length > 0) {
        if (Framework7.device.ios) {
            if ((Framework7.device.iphone || Framework7.device.ipod)) {
                if (!appDetail.supportedDevice.toLowerCase().includes('iphone')) {
                    message = '"' + appDetail.name + '" not compatible with ' + (myApp.device.iphone ? 'iPhone.' : 'iPod.');
                }
            } else if (Framework7.device.ipad) {
                if (!appDetail.supportedDevice.toLowerCase().includes('ipad')) {
                    message = '"' + appDetail.name + '" not optimised for iPad. But you still can install the application.';
                }
            }
            if (parseFloat(Framework7.device.osVersion) < parseFloat(appDetail.minOSVersion)) {
                message = '"' + appDetail.name + '" only compatible with iOS ' + appDetail.minOSVersion + ' or higher.';
            }
        } else {
            message += 'Open this page on your iOS device to install the "' + appDetail.name + '".';
        }
        if (message.length > 0 && appDetail.name && firstLoad) {
            var toastWithButton = app.toast.create({
                text: message,
                closeButton: true,
            });
            toastWithButton.open();
        }
    }
    //hide loader
    app.preloader.hide();

    //resolve
    resolve(
        {
            component: HomePage,
        },
        {
            context: {
                appDetail
            }
        }
    );
}

function simplifedAppResponse(appDetail, from, to) {
    try {
        to.menifest = from.manifestLink.split("dropbox.com")[1];

        //app details
        to.name = from.name;
        to.identifier = from.identifier;
        to.version = from.version;
        to.build = from.build;
        to.download = from.ipaFileLink;

        //more details
        if (to) {
            to.uploadDate = getDateFromTimeStamp(from.timestamp);
            to.buildType = from.buildtype;
            to.fileSize = from.ipafilesize;
            to.minOSVersion = from.minosversion;
            to.supportedDevice = from.supporteddevice;
        }

        if (from.mobileprovision) {
            to.provisonCreationDate = getDateFromTimeStamp(from.mobileprovision.createdate);
            to.provisonExpirationDate = getDateFromTimeStamp(from.mobileprovision.expirationdata);
            to.teamName = from.mobileprovision.teamname;
            to.devicesUDIDs = from.mobileprovision.devicesudid;
        }
    }
    catch (err) {
        appDetail.errorMessage = unknownErrorMessage;
        appDetail.error = err;
        router.appDetail = appDetail;
        processUsing(app, appDetail, resolve, reject, true);
    }
}

//Get date
function getDateFromTimeStamp(timestamp) {
    var date = new Date(timestamp * 1000);
    var hours = date.getHours();
    var minutes = date.getMinutes();
    var ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    hours = hours < 10 ? '0' + hours : hours;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    var strTime = date.toLocaleDateString() + ', ' + hours + ':' + minutes + ' ' + ampm;
    return strTime;
}


export default appRequest;