import AboutPage from '../pages/about.f7.html';
import HelpPage from '../pages/help.f7.html';
import InstallPage from '../pages/install.f7.html';
import DetailsPage from '../pages/details.f7.html';
import ProvisionedDevices from '../pages/provisioneddevices.f7.html';
import DownloadPage from '../pages/download.f7.html';
import QRCodePage from '../pages/qr.f7.html';
import OldBuildPage from '../pages/oldbuild.f7.html';
import NotFoundPage from '../pages/404.f7.html';

import appRequest from './appRequest.js';

const itmsServicesURI = "itms-services://?action=download-manifest&url=https://dl.dropboxusercontent.com";

var QRCode = require('qrcode');

var routes = [
  {
    path: '/',
    keepAlive: true,
    async: appRequest
  },
  {
    path: '/about/',
    component: AboutPage,
  },
  {
    path: '/help/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      resolve({ component: HelpPage }, { context: { appDetail } });
    }
  },
  {
    path: '/install/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      window.location.href = itmsServicesURI + appDetail.menifest;
      resolve({ component: InstallPage }, { context: { appDetail } });
    }
  },
  {
    path: '/install/:index/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      var requestedVersion = appDetail.allVersions[routeTo.params.index];
      var actionButtons = [];
      actionButtons.push({
        text: 'Install',
        onClick: function () {
          window.location.href = itmsServicesURI + requestedVersion.menifest;
          resolve({ component: InstallPage }, { context: { appDetail: requestedVersion } });
        }
      });
      if (requestedVersion.download) {
        actionButtons.push({
          text: 'Download',
          onClick: function () {
            window.location.href = requestedVersion.download;
            resolve({ component: DownloadPage }, { context: { appDetail: requestedVersion } });
          }
        });
      }
      actionButtons.push({
        text: 'More Details',
        onClick: function () {
          resolve({ component: DetailsPage }, { context: { appDetail: requestedVersion } });
        }
      });
      var buildActions = this.app.actions.create({
        buttons: [
          actionButtons,
          [
            {
              text: 'Cancel',
              color: 'red',
              onClick: function () {
                reject();
              }
            },
          ]
        ],
        on: {
          close: function () {
            reject();
          }
        }
      });
      buildActions.open();
    }
  },
  {
    path: '/details/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      resolve({ component: DetailsPage }, { context: { appDetail } });
    }
  },
  {
    path: '/provisioneddevices/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      resolve({ component: ProvisionedDevices }, { context: { appDetail } });
    }
  },
  {
    path: '/download/',
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      window.location.href = appDetail.download;
      resolve({ component: DownloadPage }, { context: { appDetail } });
    }
  },
  {
    path: '/qr/',
    keepAlive: true,
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      resolve({ component: QRCodePage }, { context: { appDetail } });
    },
    on: {
      pageAfterIn: function (event, page) {
        var canvas = document.getElementById('qr-canvas')
        QRCode.toCanvas(canvas, window.location.href, function (error) {
          if (error) console.error('QR-Code Error - ', error)
        });
      },
    }
  },
  {
    path: '/old-build/',
    keepAlive: true,
    async: function (routeTo, routeFrom, resolve, reject) {
      var appDetail = this.appDetail;
      resolve({ component: OldBuildPage }, { context: { appDetail } });
    }
  },
  {
    path: '(.*)',
    component: NotFoundPage,
  },
];

export default routes;